import React from 'react'
import { connect } from 'react-redux'
import { Field, reduxForm } from 'redux-form'
import { maxLengthCreator, requiredField } from '../../../utils/validators/validators'
import { LoginInput } from '../../Common/FormsControls/FormsControls'
import { login } from '../../../redux/authReducer'
import style from './LoginPage.module.css'
import { Redirect } from 'react-router-dom'

const LoginPage = (props) => {
  const onSubmit = (FormData) => {
    props.login(FormData.email, FormData.password, FormData.rememberMe)
  }

  if (props.isAuth) {
    return <Redirect to='/home' />
  }

  return (
    <div>
      <div className={style.main}>
        <div className={style.title}>Login</div>
        <LoginReduxForm onSubmit={onSubmit} />
      </div>
    </div>
  )
}

const maxLength20 = maxLengthCreator(20)

const LoginForm = (props) => {
  return(
    <form onSubmit={props.handleSubmit}>
          <div>
            <Field validate={[requiredField]} placeholder='Почта' name='email' component={LoginInput} />
          </div>
          <div>
            <Field validate={[requiredField]} placeholder='Пароль' name='password' type='password' component={LoginInput} />
          </div>
          <div>
            <Field type='checkbox' name='rememberMe' component={LoginInput} /> запомнить меня
          </div>
          <div>
            <button>Войти</button>
          </div>
    </form>
  )
}

const LoginReduxForm = reduxForm({
  form: 'login'
}) (LoginForm)

const mapStateToProps = (state) => ({
  isAuth: state.auth.isAuth
})

export default connect(mapStateToProps, {login}) (LoginPage)
